.container {
  margin: 0 20px 0 20px;
  padding: 16 px;
}
.div_grid_weeks {
  border-radius: 8px;
  background: #ffffff;
  border: 0.5px solid rgba(220, 227, 229, 0.8);
  height: 50px;
  margin-top: 24px;
  margin-bottom: 32px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
}
.div_grid_day {
  background: #ffffff;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  border-radius: 8px;
}
.div_grid_day :first-child{
  border-radius: 8px 0 0 0 ;
}
.div_grid_day :nth-child(7){
  border-radius:0 8px 0 0 ;
}
.div_grid_day :last-child{
  border-radius:0 0 8px  0
}
.div_grid_day :nth-last-child(7){
  border-radius:0 0  0 8px
}
.weeks_iteam {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  border: 1px solid transparent;
  border-radius: 8px;
  background: #ffffff;
  margin: auto;
}
.link {
  display: block;
  text-decoration: none;

  width: 100%;
  height: 100%;
}
.container_link {
  height: 125px;
  color: #343434;
  padding: 0;
  margin: 0;
  background: #ffffff;
  border: 1px solid rgba(220, 227, 229, 0.8);
}
.date {
      display: block;
